import React, { useEffect, useState } from "react"
import styled from "styled-components"
import axios from "axios"
import { useDispatch } from "react-redux"
import { useHistory } from "react-router-dom"
import { changeMapPage } from "../actions/index"
import FindAccountModal from "../components/FindAccountModal"

const Outer = styled.div`
    width: 100%;
    height: 100%;
`

const Form = styled.div`
    width: 400px;
    height: auto;
    margin: 4rem auto 0 auto;

    #title {
        font-size: 1.5rem;
    }
`

const Div1 = styled.div`
    margin-bottom: 2rem;
    text-align: center;
`
const Div2 = styled.div`
    ul {
        list-style: none;
    }
    li {
        margin: 1rem 0;
        display: flex;
        padding: 0.5rem 0;
    }
    p {
        width: 100px;
    }
    input {
        width: 200px;
    }
    button {
        margin-left: 1rem;
        width: 4rem;
        background-color: pink;
        &:hover {
            background-color: #ff7f9f;
            color: white;
        }
    }
`
const Button = styled.button`
    display: block;
    margin: 2rem auto 0 auto;
    width: 7rem;
    height: 2rem;
    font-size: 1rem;
    background-color: pink;
        &:hover {
            background-color: #ff7f9f;
            color: white;
    }
`

let url = process.env.REACT_APP_LOCAL_URL

export default function FindAccount(){
    const history = useHistory()
    const dispatch = useDispatch()
    if (!url) {
        url = "https://thereweather.space/api"
    }

    const [inputFindInfo, setInputFindInfo] =  useState({
        findNickName: "",
        authEmail: "",
        authCode: "",
    })
    
    // 
    const [isOpen, setIsOpen] = useState(false)
    
    const [userData, setUserData] = useState({
        nickName: "",
        user_id: ""
    })

    useEffect(() => {
        dispatch(changeMapPage(false))
    }, [])

    const ChangeHanlder = (key) => (e) => {
        setInputFindInfo({
            ...inputFindInfo,
            [key]: e.target.value,
        })
    }

    function sendEmail() {
      //console.log("  ")
      if(inputFindInfo.findNickName && !inputFindInfo.authCode){
        axios({
          url: url + "/findid",
          method: "post",
          headers: {
            "Content-Type": "application/json",
          },
          data: {
            temporary_id: inputFindInfo.findNickName,
            email: inputFindInfo.authEmail
          },
          withCredentials: true
        })
        .then((res) => {
            //console.log(res)
            if(res.data === "no results"){
                alert("  .")
            }
            else if(res.data.status === "success"){
                alert(" . 50 :)")
            }else{
                alert("  .")
            }   
        })
      }else{
        alert("  .")
      }
    }

    function verification() {
      //console.log(" ")
      axios({
        url: url + "/users/auth",
        method: "put",
        headers: {
          "Content-Type": "application/json", 
        },
        data: {
          temporary_id: inputFindInfo.findNickName,
          email: inputFindInfo.authEmail,
          code: inputFindInfo.authCode
        },
        withCredentials: true
      })
      .then((res) => {
        console.log(res.data)
        if(res.data === true){ 
            alert("  .")
        }else{
            alert("  .  :)")
        }
      })
    }

    function findAccountId() {
      //console.log("  ")

      if(inputFindInfo.findNickName && inputFindInfo.authEmail && inputFindInfo.authCode){
        //console.log("")
        axios({
            url: url + "/findaccount",
            method: "post",
            headers: {
                "Content-Type": "application/json",
              },
            data: {
              nickName: inputFindInfo.findNickName,
              email: inputFindInfo.authEmail,
            },
            withCredentials: true
        })
        .then((res) => {
            //,    
            console.log("",res.data)
            return setUserData((prev) => res.data) 
            // alert(res.data.nickName)
        })
        setIsOpen(true)
       }else{
           alert("   .")
       }

      //, ,      
      //     -> , ,  
       
    }

    const closeModal = () => {
        setIsOpen(false)
    }
    const loginBtn = () => {
        history.push("/login")
    }

    return (
        <Outer>
            <Form>
                <Div1>
                    <p id="title"> </p>
                    <p> </p>
                </Div1>

                <Div2>
                    <ul>
                        <li>
                            <p></p>
                            <input type="text" onChange={ChangeHanlder("findNickName")}></input>
                        </li>
                        <li>
                            <p></p>
                            <input type="text" placeholder="ex) kimcoding@gmail.com" onChange={ChangeHanlder("authEmail")}></input>
                            <button onClick={sendEmail}></button>
                        </li>
                        <li>
                            <p></p>
                            <input type="text" onChange={ChangeHanlder("authCode")}></input>
                            <button onClick={verification}></button>
                        </li>
                    </ul>
                </Div2>
                <Button onClick={findAccountId}> </Button>
                { isOpen? 
                    (<FindAccountModal
                        closeBtn={closeModal}
                        loginBtn={loginBtn}
                        userId={userData.user_id}
                        userNickName={userData.nickName}
                    />
                    ) 
                    : null
                }
                
            </Form>
        </Outer>
    )
}